body {
  margin: 0;
  padding: 0;
  height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logo {
  left: 2rem;
  top: 2rem;
  position: absolute;
  width: 0;
  height: 0;
  border-top: 40px solid transparent;
  border-bottom: 40px solid transparent;
  border-left: 60px solid #6CC4EE;
  background: transparent;
}

.logo h3 {
  top: -60px;
  left: -63px;
  position: absolute;
  font-family: 'Open Sans', sans-serif;
  font-size: 35px;
  font-weight: 800;
  color: #fff;
}
.login-form {
  padding-top: 2.5rem;
  box-sizing: border-box;
  position: absolute;
  width: 350px;
  height: 100%;
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  align-self: center;
  background: #fff;
  -webkit-transition: .5s;
  transition: .5s;
  top: 0;
  right: 0;
}

.login-hide {
  right: 350px;
}

.login-form input,
.login-form button {
  display: block;
  margin: auto;
}

.login-form h1,
.login-form h4 {
  text-align: center;
  color: #6CC4EE;
}

.login-form h1 span {
  background: #6CC4EE;
  color: #fff;
}

.login-form h1 {
  margin-bottom: 14px;
}

.login-form h4 {
  margin-top: 0;
  margin-bottom: 50px;
  font-size: 20px;
}

.login-form input,
.login-form button {
  width: 215px;
  height: 30px;
}

.login-form .email-input,
.login-form .password-input,
.login-form .login-submit {
  margin: 1rem auto;
}

.login-form .email-input,
.login-form .password-input {
  box-sizing: border-box;
  border: 2px solid #D1D1D1;
  font-family: 'Open Sans', sans-serif;
  font-size: 12px;
  padding-left: 2rem;
}

.login-form .email-input:placeholder,
.login-form .password-input:placeholder {
  color: #D1D1D1;
}

.login-form .email-icon,
.login-form .password-icon {
  width: 16px;
  left: 75px;
  position: absolute;
}

.login-form .email-icon {
  top: 189px;
}

.login-form .password-icon {
  top: 234px;
}

.login-form .login-submit {
  margin-bottom: 0;
  background: #6CC4EE;
  border: none;
  color: #fff;
  border-radius: 20px;
  font-size: 12px;
  -webkit-transition: .3s;
  transition: .3s;
}

.login-form .sign-up-button {
  background: transparent;
  border: none;
  color: #6CC4EE;
  border-radius: 20px;
  -webkit-transition: .2s;
  transition: .2s;
  margin-top: .5rem;
}

.login-social-wrapper {
  width: 200px;
  margin: auto;
  display: flex;
  justify-content: center;
}

.login-social {
  display: block;
  margin: 2rem;
  color: #6CC4EE;
  font-size: 40px;
  -webkit-transition: .2s;
  transition: .2s;
}

.login-social:hover {
  cursor: pointer;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.login-form .skip-login {
  position: absolute;
  border: none;
  color: #6CC4EE;
  border-radius: 20px;
  font-size: 12px;
  text-decoration: none;
  bottom: 20px;
  left: 0;
  right: 0;
  -webkit-transition: .2s;
  transition: .2s;
}

.login-form .login-submit:hover {
  color: #6CC4EE;
  background: #fff;
  box-shadow: 0 0 4px #6CC4EE;
}

.login-form .skip-login:hover,
.login-form .sign-up-button:hover {
  color: #fff;
  background: #6CC4EE;
}

.login-error-wrapper {
  position: absolute;
  width: 350px;
  top: 308px;
  background: #fff;
  left: -350px;
  right: 0;
  -webkit-transition: .25s;
  transition: .25s;
  opacity: 0;
}

.login-error-wrapper p {
  font-size: 12px;
  text-align: center;
}

.login-error-active {
  opacity: 1;
  left: 0;
}
.sign-up-form {
  padding-top: 2.5rem;
  box-sizing: border-box;
  position: absolute;
  top: 0;
  left: 0;
  width: 350px;
  height: 100%;
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  align-self: center;
  background: #fff;
  -webkit-transition: .5s;
  transition: .5s;
}

.signup-hide {
  left: 350px;
  top: 0;
}

.sign-up-form input,
.sign-up-form button {
  display: block;
  margin: auto;
}

.sign-up-form h1,
.sign-up-form h4 {
  text-align: center;
  color: #6CC4EE;
}

.sign-up-form h1 span {
  background: #6CC4EE;
  color: #fff;
}

.sign-up-form h1 {
  margin-bottom: 14px;
}

.sign-up-form h4 {
  margin-top: 0;
  margin-bottom: 50px;
  font-size: 20px;
}

.sign-up-form input,
.sign-up-form button {
  width: 215px;
  height: 30px;
}

.sign-up-form .email-input,
.sign-up-form .password-input,
.sign-up-form .sign-up-submit {
  margin: 1rem auto;
}

.sign-up-form .email-input,
.sign-up-form .password-input,
.sign-up-form .password-confirm-input,
.sign-up-form .name-input {
  box-sizing: border-box;
  border: 2px solid #D1D1D1;
  font-family: 'Open Sans', sans-serif;
  font-size: 12px;
  padding-left: 2rem;
}

.sign-up-form .email-input:placeholder,
.sign-up-form .password-input:placeholder {
  color: #D1D1D1;
}

.sign-up-form .email-icon,
.sign-up-form .password-icon,
.sign-up-form .password-confirm-icon,
.sign-up-form .user-icon {
  width: 16px;
  left: 75px;
  position: absolute;
}

.sign-up-form .user-icon {
  top: 189px;
}

.sign-up-form .email-icon {
  top: 235px;
}

.sign-up-form .password-icon {
  top: 281px;
}

.sign-up-form .password-confirm-icon {
  top: 326px;
}

.sign-up-form .create-user-btn {
  margin-top: 1rem;
  margin-bottom: 0;
  background: #6CC4EE;
  border: none;
  color: #fff;
  border-radius: 20px;
  font-size: 12px;
  -webkit-transition: .2s;
  transition: .2s;
}

.sign-up-form .skip-sign-up {
  position: absolute;
  bottom: 20px;
  left: 0;
  right: 0;
  text-decoration: none;
}

.sign-up-form .skip-sign-up-button {
  border: none;
  color: #6CC4EE;
  font-size: 12px;
  text-decoration: none;
  -webkit-transition: .2s;
  transition: .2s;
  border-radius: 20px;
}

.sign-up-form .create-user-btn:hover {
  color: #6CC4EE;
  background: #fff;
  box-shadow: 0 0 4px #6CC4EE;
}

.sign-up-form .skip-sign-up-button:hover {
  color: #fff;
  background: #6CC4EE;
}

.signup-error-wrapper,
.email-in-use-error {
  position: absolute;
  width: 350px;
  top: 400px;
  background: #fff;
  right: 0;
  left: 350px;
  -webkit-transition: .25s;
  transition: .25s;
  opacity: 0;
}

.signup-error-wrapper p,
.email-in-use-error p {
  font-size: 12px;
  text-align: center;
}

.sign-up-error-active {
  opacity: 1;
  left: 0;
}

.email-in-use-error.true {
  -webkit-animation: emailerror 5s linear;
          animation: emailerror 5s linear;
}

@-webkit-keyframes emailerror {
  0% {
    opacity: 0;
    right: 0;
    left: 350px;
  }
  10% {
    opacity: 1;
    left: 0;
    right: 0;
  }
  90% {
    opacity: 1;
    left: 0;
    right: 0;
  }
  100% {
    opacity: 0;
    right: 0;
    left: 350px;
  }
}

@keyframes emailerror {
  0% {
    opacity: 0;
    right: 0;
    left: 350px;
  }
  10% {
    opacity: 1;
    left: 0;
    right: 0;
  }
  90% {
    opacity: 1;
    left: 0;
    right: 0;
  }
  100% {
    opacity: 0;
    right: 0;
    left: 350px;
  }
}


.login-page {
  position: relative;
  display: grid;
  height: 100vh;
}

.right-side {
  position: absolute;
  top: 0;
  right: 0;
  background: #6CC4EE;
  height: 100vh;
  width: 50%;
}

.login-signup-wrapper {
  position: relative;
  top: 0;
  bottom: 0;
  width: 350px;
  height: 500px;
  margin: auto;
  padding: 0;
  box-shadow: 0px 0px 5px #D1D1D1;
  background: #fff;
  z-index: 1;
  overflow: hidden;
}

.login-footer {
  position: absolute;
  display: flex;
  bottom: .5rem;
  left: .5rem;
}

.login-footer p {
  margin: 1rem;
  color: #6CC4EE;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
}

.login-copyright {
  font-family: 'Open Sans', sans-serif;
  position: absolute;
  bottom: .5rem;
  right: 1.5rem;
  color: #fff;
  font-weight: 700;
}

.login-page *:focus {
  outline: none;
}

.login-page .login-submit:hover,
.login-page .sign-up-button:hover,
.login-page .skip-login:hover,
.login-page .create-user-btn:hover,
.login-page .skip-sign-up-button:hover {
  cursor: pointer;
}

.login-form-logo-wrapper .logo {
  display: block;
  position: relative;
  width: 60px;
  margin: 0 auto 3rem;
  display: none;
}



@media (max-width: 550px) {
  .login-form,
  .sign-up-form {
    padding: 0;
  }
  .login-signup-wrapper {
    box-shadow: none;
    padding-top: 0;
  }
  .right-side {
    display: none;
  }
  .login-page footer {
    display: none;
  }

  .login-form h1,
  .sign-up-form h1 {
    margin-bottom: 0;
  }

  .login-form h4,
  .sign-up-form h4 {
    display: none;
  }
  .login-page .logo {
    display: none;
  }
  .login-form-logo-wrapper .logo {
    display: block;
  }

}

.search-form {
  height: 36px;
  box-sizing: border-box;
}

.search-form .search-submit {
  align-self: center;
  background: url(data:image/png;base64,);
  background-size: contain;
  background-repeat: no-repeat;
  padding-top: 10px;
  width: 25px;
  height: 25px;
  border: none;
  padding: 5px 0;
  -webkit-transition: .3s;
  transition: .3s;
  outline: none;
}

.search-form .search-submit:hover {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  cursor: pointer;
}

.search-form .search-input {
  border: 2px solid #d1d1d1;
  width: 225px;
  margin: 0 .5rem;
  padding-left: 4px;
  height: 30px;
  font-size: 14px;
  color: #d1d1d1;
  outline: none;
}

.search-form .search-input:placeholder {
  color: #d1d1d1;
}
aside.filters {
}

.filters-desktop {
  display: flex;
  justify-content: center;
  justify-self: center;
  color: #6CC4EE;
}

.filters-desktop ul {
  font-family: 'Open Sans', sans-serif;
  margin: 0;
  padding: 0;
  width: 120%;
}

.filters-desktop h3 {
  margin: 0;
  font-size: 16px;
}

.filters-desktop .genre-filter,
.filters-desktop .year-filter,
.filters-desktop .rating-filter,
.filters-desktop .sort-filter {
  position: relative;
  text-align: center;
  box-sizing: border-box;
  width: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.filters-desktop .genre-filter {
  width: 120px;
}

.filters-desktop .year-filter {
  width: 40px;
  margin-right: 2rem;
}

.filters-desktop .rating-filter {
  width: 54px;
  margin-right: .4rem;
}

.filters-desktop .sort-filter {
  width: 120px;
}

.genre-filter h3:hover,
.year-filter h3:hover,
.rating-filter h3:hover,
.sort-filter h3:hover {
  cursor: pointer;
}

.year-list li:hover {
  cursor: pointer;
}


.filters-desktop .genre-list,
.filters-desktop .year-list,
.filters-desktop .rating-list,
.filters-desktop .sort-list {
  position: absolute;
  height: 0;
  top: 50px;
  overflow: hidden;
  -webkit-transition: .3s;
  transition: .3s;
  background: #fff;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
}

.filters-desktop .genre-list {
  left: -12px;
}

.filters-desktop .year-list {
  left: -4px;
}

.filters-desktop .rating-list {
  left: -6px;
}

.filters-desktop .sort-list {
  left: -12px;
}

.filters-desktop .genre-list.deployed,
.filters-desktop .year-list.deployed,
.filters-desktop .rating-list.deployed,
.filters-desktop .sort-list.deployed {
  height: 200%;
  overflow-y: scroll; 
}

.filters-desktop li {
  list-style: none;
  z-index: 9;
  font-size: 14px;
  background: #fff;
}

.filters-desktop button {
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  margin: 1rem;
  align-self: center;
  background: #6CC4EE;
  color: #fff;
  width: 75px;
  height: 30px;  
  border-radius: 15px;
  border: none;
  -webkit-transition: .3s;
  transition: .3s;
  outline: none;
}

.filters-desktop button:hover {
  cursor: pointer;
  color: #6CC4EE;
  background: #fff;
}

/*-- MOBILE MODAL WINDOW --*/

.filters-mobile {
  width: 100vw;
  height: 100vh;
  display: none;
  -webkit-transition: .4s;
  transition: .4s;
  position: absolute;
  background: #fff;
  z-index: 9;
}

.filters-mobile li {
  list-style: none;
  padding-right: 20px;
  width: 150px;
}

.filters-mobile li:hover {
  cursor: pointer;
}

.filters-mobile .genre-filter,
.filters-mobile .year-filter,
.filters-mobile .rating-filter,
.filters-mobile .sort-filter {
  color: #6CC4EE;
  margin: auto;
  position: relative;
  text-align: center;
  box-sizing: border-box;
  width: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.filters-mobile .genre-list.deployed,
.filters-mobile .year-list.deployed,
.filters-mobile .rating-list.deployed,
.filters-mobile .sort-list.deployed {
  z-index: 99;
  height: 500%;
  overflow-y: scroll; 
}

.filters-mobile button {
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  margin: 1rem auto;
  align-self: center;
  background: #6CC4EE;
  color: #fff;
  width: 75px;
  height: 30px;  
  border-radius: 15px;
  border: none;
  -webkit-transition: .3s;
  transition: .3s;
  outline: none;
}

.filters-mobile .modal-buttons {
  margin: 0 auto;
  width: 200px;
  display: flex;
  justify-content: space-between
}

.filters-mobile button:hover {
  cursor: pointer;
  color: #6CC4EE;
  background: #fff;
}

.modal-filter-label {
  color: #6CC4EE;
  text-align: center;
  font-size: 20px;
}

/* -- MODAL BUTTON CLOSE -- */
.close-modal-btn-wrapper {
  position: absolute;
  right: .8rem;
  top: .8rem;
}

.close-modal-btn {
  height: 25px;
  width: 25px;
  -webkit-transition: .3s;
  transition: .3s;
}

.close-modal-btn-wrapper:hover .close-modal-btn {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}

.close-modal-btn-wrapper:hover {
  cursor: pointer;
}


@media (max-width: 510px) {
  .filters-desktop {
    display: none;
  }

  .filters-mobile {
    display: block;
    top: 0;
    left: -200%;
  }

  .filter-wrapper.true .filters-mobile {
    left: 0;
  }

  .filters-mobile .genre-list,
.filters-mobile .year-list,
.filters-mobile .rating-list,
.filters-mobile .sort-list {
  position: absolute;
  height: 0;
  top: 35px;
  left: -40px;
  overflow: hidden;
  -webkit-transition: .3s;
  transition: .3s;
  background: #fff;
}
}
